import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';

class TableRow extends Component {

  constructor(props) {
        super(props);
        this.delete = this.delete.bind(this);
    }
    delete() {
        axios.get('http://localhost:4000/business/delete/'+this.props.obj._id)
            
            .then( window.location.reload(), console.log('Deleted'))
            .catch(err => console.log(err))
    }
  render() {
   
    return (
        <tr>
            <td>
           { this.props.obj._id}
          </td>
          <td>
            {this.props.obj.name}
          </td>
          <td>
          {this.props.obj.password}
          </td>
          
          <td>
          {this.props.obj.email}
          </td>
          <td>
            <Link to={"/edit/"+this.props.obj._id} className="editbtn"> <i className="fa fa-pencil-square-o" aria-hidden="true"></i></Link>
          </td>
          <td>
            <a href="#" onClick={this.delete} className="deletebtn"><i className="fa fa-trash" aria-hidden="true"></i></a>
          </td>
        </tr>
    );
  }
}

export default TableRow;